<template>
  <div class="container">
    <div class="content"
         :class="{ blur: blurStatus }">
      <van-row type="flex"
               justify="space-between"
               ref="movieBox">
        <van-col span="11"
                 class="content">
          <div class="leftSection"
               ref="leftSection">
            <SortCard :type="$store.state.homeSortCard.type"
                      icon="badge"
                      :name="$store.state.homeSortCard.name"
                      @func="showMask()"></SortCard>
            <transition-group name="fadeInUp">
              <div v-for="item in movieList_l"
                   :key="item.id">
                <!-- <router-link :to="{name: 'MovieInfo',query:{id : item.id}}"> -->
                <router-link :to="'/movieInfo/'+ item.id">
                  <div class="movie-card">
                    <div class="movie-cover">
                      <img :src="item.images.small"
                           alt="">
                    </div>
                    <h6 class="title">{{ item.title }}</h6>
                    <span class="score">{{ item.rating.average | fullScore }}
                    </span>
                  </div>
                </router-link>
              </div>
            </transition-group>
          </div>
        </van-col>
        <van-col span="11"
                 class="content">
          <div class="rightSection"
               ref="rightSection">
            <transition-group name="fadeInUp">
              <div v-for="item in movieList_r"
                   :key="item.id">
                <router-link :to="'/movieInfo/'+ item.id">
                  <div class="movie-card">
                    <div class="movie-cover">
                      <img :src="item.images.small"
                           alt="">
                    </div>
                    <h6 class="title">{{ item.title }}</h6>
                    <span class="score">{{ item.rating.average | fullScore }}</span>
                  </div>
                </router-link>
              </div>
            </transition-group>
          </div>
        </van-col>
      </van-row>
      <footer>
        <span>已经到底了哟</span>
      </footer>
    </div>
    <van-popup v-model="blurStatus"
               class="show-pops"
               @click-overlay="showMask">
      <h6 class="sort-title">切换分类：</h6>
      <SortCard ref="hotCard"
                type="hot"
                name="正在热映"
                @func="switchCard('hotCard')"></SortCard>
      <SortCard ref="showingCard"
                type="showing-soon"
                name="即将上映"
                @func="switchCard('showingCard')"></SortCard>
    </van-popup>
  </div>
</template>

<script>
import SortCard from "../components/SortCard";
export default {
  name: "Home",
  data() {
    return {
      blurStatus: false,
      hotMovieList: { arr: [], totalValue: 1 },
      showingMovieList: { arr: [], totalValue: 1 },
      movieList_l: [],
      movieList_r: [],
      hotStartValue: 0,
      showingStartValue: 0,
      countValue: 10,
      totalValue: 1, // 假定值，未获取情况下无法得知。
      sortCardType: "hot",
      switchData: true
    };
  },
  computed: {
    listenSortCard() {
      return this.$store.state.homeSortCard.type;
    }
  },
  watch: {
    listenSortCard: function(typeValue) {
      console.log(this.sortCardType);
      console.log(typeValue);
      if (typeValue != this.sortCardType) {
        this.sortCardType = typeValue;
        this.movieList_l = [];
        this.movieList_r = [];
        if (this.countValue != 10) {
          this.countValue = 10;
        }
        this.startGet();
      }
    }
  },
  created() {
    this.startGet();
  },
  mounted() {
    this.switchData = false;
    window.addEventListener("scroll", () => {
      let bottomOfWindow =
        document.body.offsetHeight - window.scrollY - window.innerHeight <= 200;
      if (bottomOfWindow && this.switchData) {
        this.switchData = false;
        // 开始获取数据
        // this.getMock(this.$store.state.homeSortCard.type);
        // this.getHotMock();
        this.startGet();
        console.log("到底了");
      }
    });
  },
  methods: {
    startGet() {
      if (this.sortCardType == "hot") {
        this.getMock(this.hotMovieList, "hotStartValue");
      } else if (this.sortCardType == "showing-soon") {
        this.getMock(this.showingMovieList, "showingStartValue");
      }
    },
    getMock(listArr, startArr) {
      /**
       * @listArr {Array} 要存放的容器
       * @startArr {Array} 开始值
       */
      let length = listArr.arr.length;
      if (length < listArr.totalValue) {
        console.log("没有数据，开始获取");
        console.log(
          this.$store.state.homeURL +
            this[startArr] +
            "&count=" +
            this.countValue
        );
        // 发起 Ajax 请求
        const API_PROXY = "https://bird.ioliu.cn/v1/?url=";
        this.$ajax
          .get(
            API_PROXY +
              this.$store.state.homeURL +
              this[startArr] +
              "&count=" +
              this.countValue
          )
          .then(response => {
            listArr.totalValue = response.data.total;
            if (this[startArr] >= listArr.totalValue) {
              this.switchData = false;
              this.countValue = 10;
              return false;
            } else {
              for (let i = 0; i < this.countValue; i++) {
                listArr.arr.push(response.data.subjects[i]);
              }
              this[startArr] += this.countValue;
              if (this[startArr] + this.countValue >= response.data.total) {
                this.countValue = response.data.total - this[startArr];
              }
              this.startDistMovie(listArr.arr);
              this.switchData = true;
            }
          });
      } else {
        console.log("有数据，进行分配");
        this.startDistMovie(listArr.arr);
        this.switchData = true;
      }
    },
    startDistMovie(listArr) {
      this.$store.commit("distMovie", {
        listArr: listArr,
        leftList: this.movieList_l,
        rightList: this.movieList_r
      });
    },
    showMask() {
      this.$store.commit("showMask");
      this.blurStatus = !this.blurStatus;
    },
    switchCard(scortCard) {
      let card = {
        type: this.$refs[scortCard]._props.type,
        name: this.$refs[scortCard]._props.name
      };
      this.$store.commit("switchCard", card);
      this.$store.commit("switchURL", card);
      this.showMask();
    }
  },
  components: {
    SortCard
  }
};
</script>

<style scoped>
.container {
  padding-top: 63px;
}
.content {
  background-color: transparent;
}

.movie-card {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-bottom: 40px;
  max-width: 158px;
}

.movie-card .movie-cover {
  margin-bottom: 10px;
  width: 100%;
  line-height: 0;
  border-radius: 8px;
  box-shadow: 0 6px 14px 0 rgba(0, 0, 0, 0.22);
  overflow: hidden;
}

.movie-cover img {
  width: 100%;
}

.movie-card .title {
  padding: 0;
  margin: 0 4px 0 0;
  max-width: 6em;
  font-size: 18px;
  font-weight: 500;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.movie-card .score {
  display: flex;
  line-height: 28px;
  padding-left: 20px;
  align-items: inherit;
  color: #ffd21e;
  font-size: 16px;
  font-weight: 500;
  background: url("data:image/svg+xml,%3Csvg viewBox='0 0 19 18' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.75 14.577l-5.652 2.971 1.079-6.293-4.572-4.457 6.319-.918 2.826-5.726 2.826 5.726 6.319.918-4.572 4.457 1.079 6.293z' fill='%23ffd21e'/%3E%3C/svg%3E")
    no-repeat left center;
  background-size: 18px;
}
.movie-card .score span {
  display: inline-block;
  vertical-align: middle;
}

.movie-card a {
  display: block;
  line-height: 0;
}

.van-popup {
  background: none;
}
.show-pops .sort-title {
  margin: 0 0 10px 0;
  color: rgba(0, 0, 0, 0.6);
  font-size: 18px;
  font-weight: normal;
}
.show-pops .sort {
  margin-bottom: 20px;
}
</style>


