<template>
  <div class="container">
    <div class="content">
      <van-row type="flex"
               justify="space-between">
        <van-col span="11"
                 class="content">
          <div class="leftSection"
               ref="leftSection">
            <SortCard type="top-250"
                      name="Top 250"></SortCard>
            <transition-group name="fadeInUp">
              <div v-for="topData in movieList_l"
                   :key="topData.id">
                <router-link :to="'/movieInfo/'+ topData.id">
                  <div class="movie-card">
                    <div class="movie-cover">
                      <img :src="topData.images.small"
                           :alt="topData.title">
                    </div>
                    <h6 class="title">{{ topData.title }}</h6>
                    <span class="score">{{ topData.rating.average | fullScore }}</span>
                  </div>
                </router-link>
              </div>
            </transition-group>
          </div>
        </van-col>
        <van-col span="11"
                 class="content">
          <div class="rightSection"
               ref="rightSection">
            <transition-group name="fadeInUp">
              <div v-for="topData in movieList_r"
                   :key="topData.id">
                <router-link :to="'/movieInfo/'">
                  <div class="movie-card">
                    <div class="movie-cover">
                      <img :src="topData.images.small"
                           :alt="topData.title">
                    </div>
                    <h6 class="title">{{ topData.title }}</h6>
                    <span class="score">{{ topData.rating.average | fullScore }}</span>
                  </div>
                </router-link>
              </div>
            </transition-group>
          </div>
        </van-col>
      </van-row>
      <footer>
        <span>已经到底了哟</span>
      </footer>
    </div>
  </div>
</template>

<script>
import SortCard from "../components/SortCard";
export default {
  name: "Ranking",
  data() {
    return {
      top250List: [],
      movieList_l: [],
      movieList_r: [],
      switchData: true,
      startValue: 0,
      countValue: 10,
      totalValue: 1 // 假定值，未获取情况下无法得知。
    };
  },
  created() {
    // this.getTopMock();
    this.getTop250();
  },
  mounted() {
    // 不适用于图片高度太大差异的布局
    this.switchData = false;
    window.addEventListener("scroll", () => {
      let bottomOfWindow =
        document.body.offsetHeight - window.scrollY - window.innerHeight <= 200;
      if (bottomOfWindow && this.switchData) {
        this.switchData = false;
        // 开始获取数据
        // this.getTopMock();
        this.getTop250();
        console.log("到底了");
      }
    });
  },
  methods: {
    // https://api.douban.com/v2/movie/top250?start=0&count=10
    getTop250() {
      var length = this.top250List.length;
      if (length < this.totalValue) {
        console.log("没有数据，开始获取");
        // 发起 Ajax 请求
        const API_PROXY = 'https://bird.ioliu.cn/v1/?url='
        this.$ajax
          .get(
            API_PROXY+"https://api.douban.com/v2/movie/top250?start=" +
              this.startValue +
              "&count=" +
              this.countValue
          )
          .then(response => {
            console.log(
              "https://api.douban.com/v2/movie/top250?start=" +
                this.startValue +
                "&count=" +
                this.countValue
            );
            let top250 = response.data;
            this.totalValue = top250.total;
            if (this.startValue >= this.totalValue) {
              this.switchData = false;
              return false;
            } else {
              console.log("可以继续获取");
            }
            for (let i = 0; i < this.countValue; i++) {
              this.top250List.push(top250.subjects[i]);
            }
            this.startValue += this.countValue;
            this.startDistMovie();
            this.switchData = true;
          });
      } else {
        console.log("有数据，进行分配");
        this.startDistMovie();
        this.switchData = true;
      }
    },
    getTopMock() {
      // 模拟数据
      var length = this.top250List.length;
      if (length < this.totalValue) {
        console.log("没有数据，开始获取");
        // 发起 Ajax 请求
        this.$ajax.get("/top").then(response => {
          let top250 = response.data.data;
          this.totalValue = top250.total;
          if (this.startValue >= this.totalValue) {
            this.switchData = false;
            return false;
          } else {
            console.log("可以继续获取");
          }
          for (let i = this.startValue; i < this.countValue; i++) {
            this.top250List.push(top250.subjects[i]);
          }
          this.startValue = this.countValue;
          this.countValue += 10;
          this.startDistMovie();
          this.switchData = true;
        });
      } else {
        console.log("有数据，进行分配");
        this.startDistMovie();
        this.switchData = true;
      }
    },
    startDistMovie() {
      this.$store.commit("distMovie", {
        listArr: this.top250List,
        leftList: this.movieList_l,
        rightList: this.movieList_r
      });
    }
  },
  components: {
    SortCard
  }
};
</script>

<style scoped>
.container {
  padding-top: 63px;
}
</style>


