import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Find from '@/pages/Find'
import MovieInfo from '@/pages/MovieInfo'
import Ranking from '@/pages/Ranking'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home',
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      meta: { navShow: true }
    },
    {
      path: '/find',
      name: 'Find',
      component: Find,
      meta: { navShow: true }
    },
    {
      path: '/ranking',
      name: 'Ranking',
      component: Ranking,
      meta: { navShow: true }
    },
    {
      path: '/movieInfo/:id',
      // path: '/movieInfo/',
      name: 'MovieInfo',
      component: MovieInfo,
      meta: { navShow: false }
    }
  ],
  linkActiveClass: 'tabActive'
})



