<template>
  <div class="mf-tab-wrap"
       :class="{ blur : blur }"
       @dblclick="backTop">
    <div class="container mf-tab">
      <div class="tab-item">
        <router-link to="/home">首页</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/find">发现</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ranking">排行</router-link>
      </div>
      <div class="tab-item mf-search"><a href="###"></a></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      blur: false
    };
  },
  methods: {
    backTop() {
      if (document.documentElement.scrollTop > 0) {
        clearInterval(timer);
        let timer = setInterval(() => {
          let current = document.documentElement.scrollTop;
          let step = (0 - current) / 10;
          step = step > 0 ? Math.ceil(step) : Math.floor(step);
          current += step;
          if(current < 0){
            current = 0;
          }
          document.documentElement.scrollTop = current;
          if (current == 0) {
            clearInterval(timer);
          }
        },15);
      }
    },
  },
  mounted() {},
  created() {
    // console.log(this.$emit('edit',showBlur),
  },
  computed: {
    listenMask() {
      return this.$store.state.maskStatus;
    }
  },
  watch: {
    listenMask(status) {
      this.blur = status;
    }
  }
};
</script>

<style scoped>
.mf-tab-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
}
.mf-tab {
  display: flex;
  height: 45px;
  align-items: center;
}

.mf-tab a {
  display: block;
  text-align: center;
  color: #afafaf;
  font-size: 16px;
  transition: all 0.3s;
}
.mf-tab .tab-item {
  width: 42px;
  margin-right: 20px;
}
.mf-tab .mf-search {
  height: 100%;
  margin-left: auto;
  margin-right: 0;
}
.mf-tab .mf-search a {
  height: 100%;
  background: url("data:image/svg+xml,%3Csvg width='100%' height='100%' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23DDE1E1' fill-rule='evenodd'%3E%3Cpath d='M9 18A9 9 0 1 1 9 0a9 9 0 0 1 0 18zm0-1.77A7.23 7.23 0 1 0 9 1.77a7.23 7.23 0 0 0 0 14.46z' fill-rule='nonzero'/%3E%3Cpath d='M14.232 15.452a.863.863 0 0 1 1.22-1.22l4.316 4.316a.863.863 0 0 1-1.22 1.22l-4.316-4.316z'/%3E%3C/g%3E%3C/svg%3E")
    no-repeat center right;
  background-size: 20px;
}
.mf-tab .tabActive {
  font-weight: 500;
  transform: scale(1.35);
  color: #000;
  transition: transform 0.1s;
}
.mf-tab .tabActive::after {
  content: "";
  display: block;
  margin: 0 auto;
  width: 18px;
  height: 2px;
  border-radius: 3px;
  background: #000;
}
</style>


