export default {
  showMask(state) {
    state.maskStatus = !state.maskStatus;
  },
  switchCard(state, card) {
    state.homeSortCard.type = card.type;
    state.homeSortCard.name = card.name;
  },
  switchURL(state, card) {
    if (card.type == "hot") {
      console.log("发起真实请求");
      state.homeURL = "https://api.douban.com/v2/movie/in_theaters?city=南昌&start="
    } else if (card.type == "showing-soon") {
      console.log("发起真实请求");
      state.homeURL = "https://api.douban.com/v2/movie/coming_soon?city=南昌&start="
    }
  },
  // 判断页面是否滚动到底部
  getTop(state, obj) {
    let iTop = 0;
    while (obj) {
      iTop += obj.offsetTop;
      obj = obj.offsetParent;
    }
    return iTop;
  },

  // 数据分配
  distMovie(state, distData) {
    /**
     * 分配数据:
     * distData 对象：
     * listArr: 数据存储容器
     * leftList: 左侧容器
     * rightList： 右侧容器
     */

    // 如果两个容器内有数据，再次遍历会把重复的数据写入容器。
    // 仅在再一次获取数据时才有用。
    console.log("开始分配数据");
    let listLength = distData.leftList.length + distData.rightList.length;
    let listArrLength = distData.listArr.length;

    // 分配到两个容器内
    for (let i = listLength; i < listArrLength; i++) {
      if (i % 2 == 0) {
        distData.rightList.push(distData.listArr[i]);
      } else {
        distData.leftList.push(distData.listArr[i]);
      }
    }
  },
}


