<template>
  <div class="movie-page">
    <!-- 封面 -->
    <div class="movie-page-cover">
      <img :src="movieCover"
           alt="">
           <!-- 渐变样式 -->
      <div class="mask"></div>
    </div>
    <div class="movie-page-info container">
      <!-- 电影名称，字数超过6个自动缩小 -->
      <h2 class="movie-name"
          :class="{ maxName : movieTitle.length>6 }">{{ movieTitle }}</h2>
          <!-- 年份/原名 -->
      <h6 class="director">
        {{ movieInfo.year }}&nbsp;-&nbsp;{{movieInfo.original_title}}
      </h6>
      <!-- 星级评分 -->
      <div class="movie-star">
        <van-rate v-model="value"
                  allow-half
                  void-icon="star"
                  void-color="#eee"
                  readonly />
        <span>{{ value*2 }}</span>
      </div>
      <div class="genres">
        <!-- 电影类型 -->
        <span v-for="(genres,i) in movieInfo.genres" :key="i">{{genres}}</span>
      </div>
      <!-- 折叠列表 -->
      <transition name="bounce">
        <div class="other-info" v-show="moreInfo">
          <p class="aka">又名：<span v-for="(aka,i) in movieInfo.aka" :key="i">{{aka}}</span></p>
          <p>制片国家/地区：<span v-for="(item,i) in movieInfo.countries" :key="i">{{item}}</span></p>
        </div>
      </transition>
      <div class="more"
           @click="more">
        <span :class="{ 'open' : !moreInfo, 'close' : moreInfo }">{{ moreText }}</span>
      </div>
      <h6 class="intro">{{movieInfo.summary}}</h6>
    </div>
    <div class="figure-wrap dir container">
      <h6>导演：</h6>
      <div class="scroll-wrap">
        <figure v-for="(dir,j) in movieInfo.directors" :key="j">
          <img :src="dir.avatars.small" :alt="dir.name">
          <p>{{ dir.name }}</p>
        </figure>
      </div>
    </div>
    <div class="figure-wrap dir container">
      <h6>演职员：</h6>
      <div class="scroll-wrap">
        <figure v-for="(casts,x) in movieInfo.casts" :key="x">
          <img :src="casts.avatars.small" :alt="casts.name">
          <p>{{ casts.name }}</p>
        </figure>
      </div>
    </div>
    <footer>
      <span>已经到底了哟</span>
    </footer>
    <transition name="fadeInUp">
      <button class="close-btn"
              v-show="closeBtn"
              @click="back">关闭</button>
    </transition>
  </div>
</template>

<script>
export default {
  name: "MovieInfo",
  data() {
    return {
      movieID: this.$route.params.id,
      movieTitle: "",
      movieInfo: {},
      movieCover: "",
      moreInfo: false,
      moreText: "更多信息",
      value: 0,
      closeBtn: false
    };
  },
  created() {
    this.getMovieInfo();
    // this.getInfoMock();
  },
  mounted() {
    window.addEventListener(
      "scroll",
      () => {
        if (window.scrollY > 100) {
          this.closeBtn = true;
        } else {
          this.closeBtn = false;
        }
      },
      true
    );
  },
  methods: {
    getMovieInfo() {
      // const axios = require("axios");
      const API_PROXY = "https://bird.ioliu.cn/v1/?url=";
      this.$ajax
        .get(
          API_PROXY + "https://api.douban.com/v2/movie/subject/" + this.movieID
        )
        .then(response => {
          this.movieInfo = {};
          // 电影名称
          this.movieTitle = response.data.title;
          // 星级
          this.value = response.data.rating.average / 2;
          // 获取高清图片
          this.movieCover = response.data.images.large.replace(
            /s_ratio_poster/g,
            "l_ratio_poster"
          );
          // 所有信息
          this.movieInfo = response.data;
        });
    },
    getInfoMock(){
      this.$ajax.get("/info")
        .then(response => {
          this.movieTitle = response.data.data.title;
          // 星级
          this.value = response.data.data.rating.average / 2;
          // 获取高清图片
          this.movieCover = response.data.data.images.large.replace(
            /s_ratio_poster/g,
            "l_ratio_poster"
          );
          this.movieInfo = response.data.data;
        });
    },
    more() {
      this.moreInfo = !this.moreInfo;
      if (this.moreInfo) {
        this.moreText = "收起";
      } else {
        this.moreText = "更多信息";
      }
    },
    back() {
      this.$router.go(-1);
    }
  },
  filters: {}
};
</script>

<style scoped>
.movie-page-cover {
  position: relative;
  line-height: 1;
  min-height: 500px;
  background-color: #ddd;
}
.movie-page-cover .mask {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 182px;
  background-image: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0) 0%,
    #fff 70%
  );
}
.movie-page-cover img {
  width: 100%;
  user-select: none;
  background-image: linear-gradient(180deg, rgba(0,0,0,0.00) 38%, rgba(255,255,255,0.73) 80%, #FFFFFF 98%);
}

.movie-page h2,
h6 {
  padding: 0;
  margin: 0;
}

.movie-page-info {
  position: relative;
}
.movie-page .movie-page-info .maxName {
  font-size: 28px;
}
.movie-page-info .movie-name {
  margin: -50px 0 0 0;
  font-size: 36px;
  text-align: center;
}
.movie-page-info .director {
  /* margin: 10px 0 0 0; */
  font-size: 14px;
  color: #9fa0a1;
  line-height: 1;
  text-align: center;
  font-weight: 500;
  -webkit-font-smoothing: antialiased;
}
.movie-page-info .director > span:not(:last-child)::after{
  content: " / ";
}

.movie-star {
  margin: 4px 0;
  line-height: 0;
  text-align: center;
}
.movie-star span {
  color: #ffd21e;
  font-size: 18px;
  font-weight: 500;
  vertical-align: middle;
}
.van-rate {
  display: inline-block;
  vertical-align: middle;
}
.van-rate,
.van-rate__item {
  line-height: 0;
}

.movie-page-info .genres {
  padding: 5px 0;
  font-size: 12px;
  text-align: center;
}
.movie-page-info .genres span{
  display: inline-block;
  margin: 0 4px;
  padding: 0 12px;
  color: #808080;
  line-height: 28px;
  font-weight: bold;
  border-radius: 100px;
  background-color: #f0f2f5;
}

.more {
  margin-top: 6px;
  color: #8e8f90;
  font-size: 14px;
  text-align: center;
  user-select: none;
}
.more span {
  display: inline-block;
  padding-bottom: 10px;
}
.more .open {
  background: url("data:image/svg+xml,%3Csvg viewBox='0 0 13 8' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.5 4.906l4.396-4.544c.462-.477 1.222-.49 1.7-.028l.028.028.081.084c.451.466.451 1.205 0 1.671l-5.26 5.437-.081.084c-.462.477-1.222.49-1.7.028l-.028-.028-5.341-5.521c-.451-.466-.451-1.205 0-1.671l.081-.084c.462-.477 1.222-.49 1.7-.028l.028.028 4.396 4.544z' fill='%238E8F90'/%3E%3C/svg%3E")
    no-repeat center 22px;
  background-size: 13px;
}
.more .close {
  background: url("data:image/svg+xml,%3Csvg viewBox='0 0 15 10' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.5 4.094l-4.396 4.544c-.462.477-1.222.49-1.7.028l-.028-.028-.081-.084c-.451-.466-.451-1.205 0-1.671l5.26-5.437.081-.084c.462-.477 1.222-.49 1.7-.028l.028.028 5.341 5.521c.451.466.451 1.205 0 1.671l-.081.084c-.462.477-1.222.49-1.7.028l-.028-.028-4.396-4.544z' fill='%238E8F90' fill-rule='nonzero'/%3E%3C/svg%3E")
    no-repeat center 22px;
  background-size: 13px;
}

.bounce-enter-active {
  animation: bounce-in 0.2s;
}
.bounce-leave-active {
  animation: bounce-in 0.2s reverse;
}
@keyframes bounce-in {
  0% {
    height: 0;
    opacity: 0;
    overflow: hidden;
  }
  10%{
    height: 10px;
    opacity: .1;
    overflow: hidden;
  }
  20% {
    height: 20px;
    opacity: .2;
    overflow: hidden;
  }
  30% {
    height: 30px;
    opacity: .3;
    overflow: hidden;
  }
  45% {
    height: 40px;
    opacity: .4;
    overflow: hidden;
  }
  50%{
    height: 50px;
    opacity: .5;
    overflow: hidden;
  }
  60%{
    height: 60px;
    opacity: .6;
    overflow: hidden;
  }
  70%{
    height: 70px;
    opacity: .7;
    overflow: hidden;
  }
  80%{
    height: 80px;
    opacity: .8;
    overflow: hidden;
  }
  90%{
    height: 90px;
    opacity: .9;
    overflow: hidden;
  }
  100% {
    height: 120px;
    opacity: 1;
    overflow: hidden;
  }
}

.movie-page-info .other-info {
  padding: 0 50px;
  text-align: center;
  font-size: 14px;
}
.movie-page-info .other-info p {
  margin: 4px 0;
  color: #808080;
  font-weight: bold;
}

.other-info p > span{
  color: #8e8f90;
  font-weight: normal
}

.other-info .aka > span:not(:last-child)::after {
  content: " | ";
  padding: 0 2px;
  color: #808080;
  font-weight: bold;
}

.movie-page-info .intro {
  margin-top: 11px;
  color: #acadaf;
  font-size: 14px;
  line-height: 1.5;
  font-weight: normal;
  text-align: justify;
  text-indent: 2em;
}

.figure-wrap {
  margin-top: 12px;
}
.figure-wrap h6 {
  margin-bottom: 8px;
  font-size: 18px;
}
.figure-wrap .scroll-wrap{
    overflow-x: scroll;
    overflow-y: hidden;
    max-height: 168px;
    white-space: nowrap;
}
.figure-wrap figure {
  display: inline-block;
  margin: 0;
  line-height: 1;
}
.figure-wrap figure:not(:last-child){
  margin-right: 10px;
}
.figure-wrap img{
  width: 100px;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.2);
}
.figure-wrap p{
  margin: 0;
  width: 100px;
  text-align: center;
  text-overflow: ellipsis;
  font-size: 14px;
  font-weight: bold;
  overflow: hidden;
}

.fadeInUp-enter-active {
  animation: fadeInUp 0.3s;
}
.fadeInUp-leave-active {
  animation: fadeInUp 0.3s reverse;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
footer {
  margin-top: 40px;
}
.close-btn {
  position: fixed;
  bottom: 34px;
  left: 50%;
  padding-left: 42px;
  margin-left: -50px;
  width: 100px;
  height: 40px;
  color: #fff;
  font-size: 20px;
  text-align: left;
  border-radius: 100px;
  border: none;
  background: #212121
    url("data:image/svg+xml,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.564 16.091l3.313 3.313c.407.407.407 1.066 0 1.473-.407.407-1.066.407-1.473 0l-3.313-3.313-3.313 3.313c-.407.407-1.066.407-1.473 0-.407-.407-.407-1.066 0-1.473l3.313-3.313-3.313-3.313c-.407-.407-.407-1.066 0-1.473.407-.407 1.066-.407 1.473 0l3.313 3.313 3.313-3.313c.407-.407 1.066-.407 1.473 0 .407.407.407 1.066 0 1.473l-3.313 3.313zm-1.564 15.909c-8.837 0-16-7.163-16-16s7.163-16 16-16 16 7.163 16 16-7.163 16-16 16zm0-1.455c8.033 0 14.545-6.512 14.545-14.545 0-8.033-6.512-14.545-14.545-14.545-8.033 0-14.545 6.512-14.545 14.545 0 8.033 6.512 14.545 14.545 14.545z' fill='%23fff'/%3E%3C/svg%3E")
    no-repeat 4px center;
  background-size: 32px;
}
</style>


